.App {
    display: flex;
    align-items: center;
    min-height: 100vh;
    justify-content: center;
    /* flex-direction: column; */
    flex-wrap: wrap;
    margin: 2rem 2rem;
    padding: 1rem;
    flex: 2;
  }
  .title{
    text-shadow: 0px 0px 3px rgb(255,255,255,0.99);
    /* width: 100%; */
    flex-wrap: nowrap;
    position: absolute;
    top: 10px;
    margin: auto;
    z-index: 500;
    background: linear-gradient(to top right, rgb(255,255,255,0.99));  
  }
  .searchForm {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  
  .searchBar {
    border-radius: 2rem;
    width: 30%;
    margin: 1rem;
    padding: 0.5rem;
  }
  .searchBar:hover{
    box-shadow: 0 0 5px rgb(255,255,255,0.6);
  }
  .searchButton {
    border-radius: 2rem;
    margin: 1rem;
    padding: 0.5rem;
  }
  
  .searchButton:hover {
    box-shadow: 0px 0px 6px rgb(255,255,255,0.6);
  }